export default {
	data() {
		return {
			user_token: '',
			TabCur: -1,
			scrollLeft: 0,
			jpuz_info: [],
			MisList: []

		};
	},
	onLoad() {
		let that = this;
		that.user_token = uni.getStorageSync("user_token");
		uni.request({
			url: that.MisUrl + '/jpuz/userGetMis',
			method: 'POST',
			header: {
				'user_token': that.user_token
			},
			data: {},
			success: res => {
				that.jpuz_info = res.data.result
			},
			fail: () => {},
			complete: () => {}
		});

	},
	methods: {
		toSquareInfo(e) {
			let that = this;
			var item = e.currentTarget.dataset.id;
			console.log(e.currentTarget.dataset.id);
			uni.setStorage({
				key: 'square_info',
				data: item
			})
			uni.navigateTo({
				url: 'mySquareInfo/mySquareInfo?misId=' + item.misId,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		tab_02(e) {
			let that = this;
			console.log(e.currentTarget.dataset.id);
			this.TabCur = e.currentTarget.dataset.id;
			var id = that.TabCur

			if (id == -1) {
				uni.request({
					url: that.MisUrl + '/jpuz/userGetMis',
					method: 'POST',
					header: {
						'user_token': that.user_token
					},
					data: {},
					success: res => {
						that.jpuz_info = res.data.result
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.request({
					url: that.MisUrl + '/jpuz/userGetMis/' + id,
					method: 'POST',
					header: {
						'user_token': that.user_token
					},
					data: {},
					success: res => {
						that.jpuz_info = res.data.result
					},
					fail: () => {},
					complete: () => {}
				});
			}


		}
	}
}
